import uniRate from "@/components/uni-rate/uni-rate.vue" //星星评分
export default {
	components:{
		uniRate
	},
	onLoad(e) {
	     this.id=e.id
	
	},
	mounted(){
		var _this=this
		uni.getStorage({
			key:"info",
			success: (res) => {
			
				this.info=JSON.parse(res.data)
					
			}
		})
		
		//ping lun
		this.request({
			url:this.config.url+"goods/comments",
			method:"post",
			data:{
				token:this.token,
				id:this.id
			},
			success:(res)=> {
				console.log(res)
				if(res.data.code==1){
					this.goodsList=res.data.data.data;
					
				}
				
			}
		})
		
	},
	data() {
		return {
			id:"",
			goodsList: [],
			headerTop: 0,
			//控制滑动效果
			typeClass: 'goods',
			
			theIndex: null,
			oldIndex: null,
			isStop: false,
			info:{}
		}
	},
	onPageScroll(e) {

	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	
	methods: {
		sayFeel() {
			uni.navigateTo({
				url: "/pages/user/keep/sayFeel/sayFeel"
			})
		},
		switchType(type) {
			// if(this.typeClass==type){
			// 	return ;
			// }
			// uni.pageScrollTo({
			// 	scrollTop:0,
			// 	duration:0
			// })
			// this.typeClass = type;
			// this.subState = this.typeClass==''?'':'show'+type;
			// setTimeout(()=>{
			// 	this.oldIndex = null;
			// 	this.theIndex = null;
			// 	this.subState = this.typeClass=='goods'?'':this.subState;
			// },200)
			this.typeClass = type;
			if (type == "goods") {
				this.subState = 1;

			} else if (type == "shop") {
				this.subState = 2;
			}
		},


		discard() {
			//丢弃
		}


	}
}
